import { createElement } from '../render.js';
import { humanizeDate, getDuration } from '../utils.js';
import { DATE_FORMAT } from '../const.js';

function createOffersTemplate(offers) {
  if (!offers.length) {
    return '';
  }

  return `
    ${offers.map((offer) => offer.checked ? `
      <li class="event__offer">
        <span class="event__offer-title">${offer.name}</span>
        &plus;&euro;&nbsp;
        <span class="event__offer-price">${offer.cost}</span>
      </li>`
    : '').join('')}
  `;
}

function createEventTemplate(event) {
  const {type, destination, offers, price, startTime, endTime, isFavorite} = event;
  const {name} = destination;

  const startEventTime = humanizeDate(startTime, DATE_FORMAT.TIME);
  const endEventTime = humanizeDate(endTime, DATE_FORMAT.TIME);
  const eventDate = humanizeDate(startTime, DATE_FORMAT.EVENT_DATE);
  const displayDate = humanizeDate(startTime, DATE_FORMAT.DISPLAY_DATE);
  const durationTime = getDuration(startTime, endTime);
  const offersTemplate = createOffersTemplate(offers);

  return `
    <li class="trip-events__item">
      <div class="event">
        <time class="event__date" datetime="${eventDate}">${displayDate.toUpperCase()}</time>
        <div class="event__type">
          <img class="event__type-icon" width="42" height="42" src="img/icons/${type.toLowerCase()}.png" alt="Event type icon">
        </div>
        <h3 class="event__title">${type} ${name}</h3>
        <div class="event__schedule">
          <p class="event__time">
            <time class="event__start-time" datetime="${startTime}">${startEventTime}</time>
            &mdash;
            <time class="event__end-time" datetime="${endTime}">${endEventTime}</time>
          </p>
          <p class="event__duration">${durationTime}</p>
        </div>
        <p class="event__price">
          &euro;&nbsp;<span class="event__price-value">${price}</span>
        </p>
        <h4 class="visually-hidden">Offers:</h4>
        <ul class="event__selected-offers">
          ${offersTemplate}
        </ul>
        <button class="event__favorite-btn ${isFavorite ? 'event__favorite-btn--active' : ''}" type="button">
          <span class="visually-hidden">Add to favorite</span>
          <svg class="event__favorite-icon" width="28" height="28" viewBox="0 0 28 28">
            <path d="M14 21l-8.22899 4.3262 1.57159-9.1631L.685209 9.67376 9.8855 8.33688 14 0l4.1145 8.33688 9.2003 1.33688-6.6574 6.48934 1.5716 9.1631L14 21z"/>
          </svg>
        </button>
        <button class="event__rollup-btn" type="button">
          <span class="visually-hidden">Open event</span>
        </button>
      </div>
    </li>
  `;
}

export default class EventView {
  constructor({event}) {
    this.event = event;
  }

  getTemplate() {
    return createEventTemplate(this.event);
  }

  getElement() {
    if(!this.element) {
      this.element = createElement(this.getTemplate());
    }

    return this.element;
  }

  removeElement() {
    this.element = null;
  }
}
